import React, {useEffect, useState} from 'react';
import {getOne} from "../api/Api";

function API_1(props) {
    const [serverData, setServerData] = useState('')

    useEffect(() => {

        getOne().then(data => {
            setServerData(data)
        })
    }, []);

    let krwList = [];
    let btcList = [];
    let usdtList = [];

    for (let key in serverData) {
        const marketKey = serverData[key].market;
        if (marketKey.startsWith("KRW-")) {
            krwList.push(serverData[key])
        } else if (marketKey.startsWith("BTC-")) {
            btcList.push(serverData[key])
        } else {
            usdtList.push(serverData[key])
        }
    }

    return (
        <div style={{border: '1px solid black', padding: '10px', marginTop: '20px', maxWidth: '400px'}}>
            {krwList.map((item, index) => (
                <div key={index} style={{display: 'flex', borderBottom: '1px solid black', padding: '10px'}}>
                    <div style={{flex: 1, borderRight: '1px solid black', padding: '5px', wordWrap: 'break-word'}}>
                        {item.korean_name} ({item.english_name})
                    </div>
                    <div style={{flex: 1, padding: '5px', wordWrap: 'break-word'}}>
                        {item.market}
                    </div>
                </div>
            ))}
        </div>
    )
}

export default API_1;